<template>
  <div class="roles">
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 添加按钮 -->
    <el-button @click="showAddRole" type="success" plain>添加角色</el-button>

    <!-- 表格 -->
    <el-table :data="rolesList" style="width: 100%">
      <el-table-column type="expand">
        <template v-slot:default="{row}">
          <el-row v-for="l1 in row.children" :key="l1.id" class="l1">
            <el-col :span="4">
              <el-tag @close="delRights(row,l1.id)" closable>{{l1.authName}}</el-tag>
            </el-col>
            <el-col :span="20">
              <el-row v-for="l2 in l1.children" :key="l2.id">
                <el-col :span="4"><el-tag @close="delRights(row,l2.id)" closable type="success">{{l2.authName}}</el-tag></el-col>
                <el-col :span="20">
                  <el-tag @close="delRights(row,l3.id)" closable v-for="l3 in l2.children" :key="l3.id" type="warning" class="l3">{{l3.authName}}</el-tag>
                </el-col>
              </el-row>
           </el-col>
          </el-row>
        </template>
      </el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column label="角色名称" prop="roleName"></el-table-column>
      <el-table-column label="描述" prop="roleDesc"></el-table-column>
      <el-table-column label="操作" prop="desc">
        <template v-slot:default="{row}">
          <el-button @click="editRole(row)" size="small" type="primary" icon="el-icon-edit" plain></el-button>
          <el-button @click="delRole(row)" size="small" type="danger" icon="el-icon-delete" plain></el-button>
          <el-button @click="showassVisible(row)" size="small" type="success" icon="el-icon-check" plain>分配权限</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分配权限对话框 -->
    <el-dialog title="提示" :visible.sync="assVisible" width="30%">

      <el-tree
      ref="tree"
      :data="data"
      show-checkbox
      node-key="id"
      default-expand-all
      :props="defaultProps">
      </el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="assVisible = false">取 消</el-button>
        <el-button type="primary" @click="assRolesVisible">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 添加or修改 角色对话框 -->
    <el-dialog
    :title="title"
    :visible.sync="addVisible"
    @close="closeDialog"
    width="40%">

      <el-form :model="roleForm" status-icon :rules="rules" ref="roleForm" label-width="80px">

        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="roleForm.roleName" placeholder="请输入角色名称"></el-input>
        </el-form-item>

        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="roleForm.roleDesc" placeholder="请输入角色描述"></el-input>
        </el-form-item>

      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      roleId: '',
      data: [],
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      rolesList: [],
      assVisible: false,
      addVisible: false,
      title: '',
      roleForm: {
        id: '',
        roleName: '',
        roleDesc: ''
      },
      rules: {
        roleName: [
          { required: true, message: '请输入活动名称', trigger: ['blur', 'change'] },
          { min: 2, max: 12, message: '长度在 2 到 12 个字符', trigger: ['blur', 'change'] }
        ],
        roleDesc: [
          { min: 2, max: 16, message: '长度在 2 到 16 个字符', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    async getRolesList () {
      const { data, meta } = await this.$axios.get('roles')
      if (meta.status === 200) {
        this.rolesList = data
      }
    },
    // 删除权限
    async delRights (row, id) {
      // console.log(row, id)
      const { data, meta } = await this.$axios.delete(`roles/${row.id}/rights/${id}`)
      if (meta.status === 200) {
        this.$message.success(meta.msg)
        row.children = data
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 删除角色
    async delRole (row) {
      try {
        await this.$confirm('此操作将删除该角色, 是否继续?', '温馨提示', {
          confirmButtonText: '删除',
          cancelButtonText: '取消',
          type: 'warning' })
        const { meta } = await this.$axios.delete(`roles/${row.id}`)
        if (meta.status === 200) {
          this.$message.success(meta.msg)
          this.getRolesList()
        } else {
          this.$message.error(meta.msg)
        }
      } catch (e) {
        console.log(e)
      }
    },
    // 树形结构渲染
    async showassVisible (row) {
      this.roleId = row.id
      this.assVisible = true
      const { data, meta } = await this.$axios.get('rights/tree')
      if (meta.status === 200) {
        this.data = data
        // 回显
        const ids = []
        // console.log(data)
        row.children.forEach(l1 => {
          l1.children.forEach(l2 => {
            l2.children.forEach(l3 => {
              ids.push(l3.id)
            })
          })
        })
        this.$refs.tree.setCheckedKeys(ids)
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 分配权限
    async assRolesVisible () {
      const ids = this.$refs.tree.getCheckedKeys() // 选中的id
      const hids = this.$refs.tree.getHalfCheckedKeys() // 半选的id
      const rids = [...ids, ...hids].join(',') // 合并
      const { meta } = await this.$axios.post(`roles/${this.roleId}/rights`, { rids })
      if (meta.status === 200) {
        this.$message.success(meta.msg)
        this.assVisible = false
        this.getRolesList()
      } else {
        this.$message.error(meta.msg)
      }
    },
    // 显示添加对话框
    showAddRole () {
      this.addVisible = true
      this.title = '添加角色'
    },
    // 显示修改对话框
    editRole (row) {
      this.addVisible = true
      this.title = '修改角色'
      this.$nextTick(() => { //  $nextTick(fn)--等dom渲染完成后调用，执行里头的函数fn
        this.roleForm.id = row.id
        this.roleForm.roleName = row.roleName
        this.roleForm.roleDesc = row.roleDesc
      })

      console.log(row)
    },
    // 重置对话框-初始状态
    closeDialog () {
      this.$refs.roleForm.resetFields()
      this.roleForm.id = ''
    },
    // 添加 or 修改
    async addRole () {
      try {
        await this.$refs.roleForm.validate() // 校验
        const flag = this.title === '添加角色'
        const url = flag ? 'roles' : `roles/${this.roleForm.id}`
        const method = flag ? 'post' : 'put'
        const { meta } = await this.$axios[method](url, this.roleForm)
        if (meta.status === 201 || meta.status === 200) {
          this.$message.success(meta.msg)
          this.addVisible = false
          this.getRolesList()
        } else {
          this.$message.error(meta.msg)
        }
      } catch (e) {
        console.log(e)
      }
    }

  }
}
</script>

<style lang="scss" scoped>
  .el-breadcrumb{
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #C5C5C5;
    margin-bottom: 10px;
  }
  .el-table{
    margin-top: 10px;
  }
  .el-tag{
    margin-bottom: 10px;
  }
  .l3{
    margin-right: 10px;
  }
  .l1{
    border-bottom: 1px solid #EBEEF5;
    padding: 10px 0;
    margin-bottom: 10px;
  }

</style>
